<template>
    <div class="contianer w-50 mx-auto border p-5">
        <h2>登入</h2>
        <label for="account" class="form-label w-50">帳號
            <input name="account" type="text" class="form-control" v-model="account">
        </label>
        <br>
        <label for="password" class="form-label w-50">密碼
            <input name="password" type="text" class="form-control" v-model="password">
        </label>
        <br>
        <button type="button" class="btn btn-primary" @click="logIn">登入</button>
    </div>
</template>

<script>
export default {
  data () {
    return {
      account: 'a0925636386@gmail.com',
      password: ''
    }
  },
  methods: {
    logIn () {
      const logInfo = {
        username: `${this.account}`,
        password: `${this.password}`
      }
      const api = `${process.env.VUE_APP_PATH}admin/signin`
      console.log(logInfo, api)
      this.$http.post(api, logInfo).then((res) => {
        console.log(res)
        if (res.data.success === true) {
          const token = res.data.token
          const expires = res.data.expired
          console.log(token)
          console.log(expires)
          document.cookie = `LogInToken=${token};expires=${new Date(expires)};`
          this.$router.push('/DashBoard/productslist')
        }
      })
    }
  }
}
</script>
